@import url(https://fonts.googleapis.com/css?family=Comfortaa:300,400,700|Montserrat&display=swap&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=Comfortaa:300,400,700|Montserrat&display=swap&subset=latin-ext);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --main-color: rgba(156, 105, 204, 0.74);
  --dark-main-color: #721497;
  --light-main-color: rgb(252, 252, 252);
  --dark-color: rgb(78, 76, 77);
  --light-color: rgb(249, 235, 255);
}

body {
  font-family: 'Montserrat', sans-serif;
  color: var(--dark-color);
  background: var(--light-color);
}

h1 {
  font-family: 'Comfortaa', sans-serif;
  font-size: 2.5rem;
  line-height: 4rem;
}

h2 {
  font-family: 'Comfortaa', sans-serif;
  font-weight: normal;
}

h3 {
  position: relative;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding-bottom: 1.5rem;
}

h3::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 1rem;
  width: 2rem;
  height: 4rem;
  background: var(--main-color);
  z-index: -1;
}


h3:first-letter {
  display: block;
  color: white;
  border-radius: 50%;
  font-size: 2rem;
  line-height: 2rem;
  padding: 0 .2rem 0 1.1rem;
  letter-spacing: 5px;
}

h4 {
  font-family: 'Comfortaa', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 5px;
}

button {
  font-family: 'Montserrat', sans-serif;
}

button:focus {
  outline: none;
}

p {
  line-height: 1.5rem;
}

*,
*::before,
*::after {
  padding: 0;
  box-sizing: border-box;
  --navi-color: rgba(148, 11, 153, 0.35) ;
  --main-color: rgba(156, 105, 204, 0.74);
  --dark-main-color: #721497;
  --light-main-color: rgb(252, 252, 252);
  --dark-color: rgb(78, 76, 77);
  --light-color: rgb(249, 235, 255);
}

body {
  font-family: "Montserrat", sans-serif;
  color: var(--dark-color);
  background: var(--light-color);
}

.headerDarkName {
  font-family: "Comfortaa", sans-serif;
  font-size: 2rem;
  line-height: 3rem;
  display: inline-block;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation: pop-up 0.8s forwards;
          animation: pop-up 0.8s forwards;

}
h1 .headerDarkName {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

h3 .headerLightName {
    -webkit-animation-delay: 2s;
            animation-delay: 2s;

}
.headerLightName {
  color: var(--main-color);
  font-size: 1.5em;
      display: inline-block;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-animation: pop-up 2s forwards;
          animation: pop-up 2s forwards;
}
.logoName {
  width: 100%;
  text-align: center;
}
.iconHeader {
  padding: 5px;
  color: var(--dark-main-color);
  -webkit-animation: enter 2s ease-in-out both;
          animation: enter 2s ease-in-out both;

}
.iconHeader :hover {
  color: var(--main-color)
}

.icon :hover {
  color: var(--main-color)
}

.icon {
padding: 5px;
color: var(--dark-main-color);
}

header img {
  border-radius: 50%;
  width: 150px;
  border: 1px solid;
    -webkit-animation: enter 2s ease-in-out both;
            animation: enter 2s ease-in-out both;
}

header {
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: flex-end;
  align-content: center;
  flex-wrap: wrap;
  padding: 2em;
  background: var(--light-color);
  background: -webkit-gradient(
    linear,
   left bottom, left top,
   color-stop(44%, var(--light-color)),
   to(var(--navi-color)) );
  background: linear-gradient(
    0deg,
   var(--light-color) 44%,
   var(--navi-color)100% );
}

#scrollDownButton a {
  padding-top: 60px;
  -webkit-animation:pop-up 3s  forwards;
          animation:pop-up 3s  forwards;

}
#scrollDownButton a span {
  position: absolute;
  left: 50%;
  width: 46px;
  height: 46px;
  margin-left: -23px;
  border: 1px solid var(--main-color);
  border-radius: 100%;
  box-sizing: border-box;
}
#scrollDownButton a span::after {
  position: absolute;
  top: 50%;
  left: 50%;
  content: '';
  width: 16px;
  height: 16px;
  margin: -12px 0 0 -8px;
  border-left: 1px solid var(--main-color);
  border-bottom: 1px solid var(--main-color);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  box-sizing: border-box;
}

#scrollDownButton:hover {
   -webkit-transform: scale(1.1);
           transform: scale(1.1);
   opacity: 0.6;
}

.socialMedia {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 5px;;
}

@-webkit-keyframes pop-up {
  0% {
    -webkit-transform: scale(0) translateY(-10px);
            transform: scale(0) translateY(-10px);
    opacity: 0;
  }

  80% {
    -webkit-transform: scale(1.2) translateY(-2px);
            transform: scale(1.2) translateY(-2px);
    opacity: 0.8;
  }

  100% {
    -webkit-transform: scale(1) translateY(0px);
            transform: scale(1) translateY(0px);
    opacity: 1;
  }
}

@keyframes pop-up {
  0% {
    -webkit-transform: scale(0) translateY(-10px);
            transform: scale(0) translateY(-10px);
    opacity: 0;
  }

  80% {
    -webkit-transform: scale(1.2) translateY(-2px);
            transform: scale(1.2) translateY(-2px);
    opacity: 0.8;
  }

  100% {
    -webkit-transform: scale(1) translateY(0px);
            transform: scale(1) translateY(0px);
    opacity: 1;
  }
}
@-webkit-keyframes enter {
  from {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0)
  }

  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}
@keyframes enter {
  from {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0)
  }

  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}

.aboutSection,
.contactSection
{
