}

.nav-links {
  text-decoration: none;
  color: var(--main-color)
}

.main-nav li {
  text-align: center;
  margin: 15px auto;
}


.navbar-toggle {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
  font-size: 24px;
}

.active {
