}

.languageOptions > button{
 padding: 10px;
  border: none;
  font: inherit;
  color: var(--light-color);
  background-color: transparent;
  cursor: pointer;
  text-transform: uppercase;
font-size: 1rem;

}
.navigationListButton{
  position: relative;
  display: block;
  margin:0 auto;
  padding: auto;
  background: none;
  border: none;
  font-size: 0.8em;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: -webkit-transform .3s ease-in-out;
  transition: -webkit-transform .3s ease-in-out;
  transition: transform .3s ease-in-out;
  transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
  color: var(--light-color)
}

@media screen and (min-width: 768px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
    height: 70px;
    align-items: center;
  }

  .main-nav {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    justify-content: flex-end;
  }

  .main-nav li {
    margin: 0;
  }

  .nav-links {
    margin-left: 40px;
  }

  .navbar-toggle {
    display: none;

  }

  .nav-links:hover {
    color: rgba(255, 255, 255, 1);
  }


.navigationListButton{
  position: relative;
  display: block;
  margin: 0 auto;
  padding: 10px 10px 0;
  background: none;
  border: none;
  font-size: 1em;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: -webkit-transform .3s ease-in-out;
  transition: -webkit-transform .3s ease-in-out;
  transition: transform .3s ease-in-out;
  transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
  color: var(--light-color)
}

.navigationListButton::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%) scaleX(0);
          transform: translateX(-50%) scaleX(0);
  width: 100%;
  height: 2px;
  background-color: var(--dark-main-color);
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.navigationListButton:hover::before {
  -webkit-transform: translateX(-50%) scaleX(.9);
          transform: translateX(-50%) scaleX(.9);
}
}

